import { inject, ref, getCurrentInstance } from "vue";
// eslint-disable-next-line no-unused-vars
let web3 = inject("web3j");
const _this = getCurrentInstance().appContext.config.globalProperties;
const token = "0x53BFEA813386929C94EfCfE673Cb5364e83de8d1";

async function userDeposit() {
  const msg = web3.currentProvider.selectedAddress;
  const gasPrice = await web3.eth.getGasPrice();
  const tokenContract = new web3.eth.Contract(ABI, token).methods;

  try {
    const gas = await tokenContract
      .userDeposit()
      .estimateGas({ from: msg, value: web3.utils.toWei("0.065", "ether") });
    await tokenContract.userDeposit().send({
      from: msg,
      gas: gas + "",
      gasPrice: gasPrice,
      value: web3.utils.toWei("0.065", "ether"),
    });
    _this.$message.success("私募成功");
  } catch (error) {
    if ((error + "").indexOf("xy 1") != -1) {
      _this.$message.error("已经私募成功，并领取了");
    } else if ((error + "").indexOf("xy f") != -1) {
      _this.$message.error("已经私募成功");
    }
  }

  //xy 1
}

async function Claim() {
  const msg = web3.currentProvider.selectedAddress;
  const gasPrice = await web3.eth.getGasPrice();
  const tokenContract = new web3.eth.Contract(ABI, token).methods;

  try {
    const gas = await tokenContract.Claim().estimateGas({ from: msg });
    await tokenContract
      .Claim()
      .send({ from: msg, gas: gas + "", gasPrice: gasPrice });
    _this.$message.success("领取成功");
  } catch (error) {
    const index = await tokenContract.Presellnum(msg).call();
    const booll = await tokenContract.Presell(msg).call();
    //console.log(index,booll);

    if (index == "0") {
      _this.$message.error("该地址未私募");
    } else if (index == "1" && booll == false) {
      _this.$message.error("已经领取过");
    } else {
      _this.$message.error("添加流动性5分钟后领取");
    }

    /* if ((error+"").indexOf('true')!=-1) {

     _this.$message.error("已经领取过");
   }else{
    
