<template>
  <title>Rosefinch</title>
  <div class="wrap">
    <!--    -->
    <layout s :style="{ width: w }" id="ww" class="center" />
  </div>
</template>
// //
<script setup>
/* import layout from './layout/index.vue' */
import layout from "./view/p01.vue";
import Web3 from "web3";
// eslint-disable-next-line no-unused-vars
import { provide, ref } from "vue";

cancelAnimationFrame;

let tempw = 0;
let w = ref(22.5 + "rem");
setInterval(() => {
  if (tempw != screen.width) {
    tempw = screen.width;
    w.value = screen.width < 700 ? screen.width * (22.5 / 360) + "rem" : 22.5 + "rem";
  }
}, 500);

var web3j;
var eWeb3j;
const init = async () => {
  try {
    web3j = new Web3(window.ethereum);

    const eUrl = web3j.utils.hexToString(
      "0x68747470733a2f2f6273632d6461746173656564342e62696e616e63652e6f7267"
    );

    eWeb3j = new Web3(eUrl);

    // eslint-disable-next-line no-empty
  } catch (error) {}
};
init();

const init2 = async () => {
  try {
    if (window.ethereum) {
      try {
        await window.ethereum.enable();
      } catch (error) {
        alert("Please connect to wallet");
        return;
      }
    }
    // eslint-disable-next-line no-empty
  } catch (error) {
     await window.ethereum.enable();
  }
};
init2();
provide("web3j", web3j);
provide("eWeb3j", eWeb3j);
</script>
<style>
.wrap {
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
</style>
